import React, { Component } from "react";
import { connect } from "react-redux";
import styles from "../App/styles.module.css";
import Form from "../App/Form";
import Filter from "../App/Filter";
import Contacts from "../App/Contacts";
import Modal from "../App/Modal";
import IconButton from "../App/IconButton";
import { ReactComponent as AddIcon } from "../App/addIcon.svg";
import { fetchContacts } from "../redux/phonebook-operations";
import selectors from "../redux/phonebook-selectors";

class Phonebook extends Component {
  state = {
    modalOpened: false,
  };

  componentDidMount() {
    this.props.fetchContacts();
  }

  toggleModal = () => {
    this.setState((prevState) => ({ modalOpened: !prevState.modalOpened }));
  };

  render() {
    const { modalOpened } = this.state;

    return (
      <div className={styles.container}>
        <h1>Phonebook</h1>
        <IconButton handleClick={this.toggleModal}>
          <AddIcon width="35" height="35" fill="#fff" />
        </IconButton>
        {modalOpened && (
          <Modal closeModal={this.toggleModal}>
            <Form onSave={this.toggleModal} />
          </Modal>
        )}

        <h2>Contacts</h2>
        <Filter />
        {this.props.isLoading && <h1 className={styles.loader}>Loading...</h1>}
        <Contacts />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  isLoading: selectors.isLoading(state),
});

const mapDispatchToProps = (dispatch) => ({
  fetchContacts: () => dispatch(fetchContacts()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Phonebook);
