import React, { Component } from "react";
import styles from "./styles.module.css";
import { connect } from "react-redux";
import { addContact } from "../redux/phonebook-operations";
import selectors from "../redux/phonebook-selectors";

class Form extends Component {
  state = {
    name: "",
    number: "",
  };

  handleInput = (event) => {
    const { name, value } = event.currentTarget;

    this.setState(() => ({ [name]: value }));
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { contacts, addValidatedContact } = this.props;

    const validationArray = contacts.filter(
      ({ name, number }) =>
        name === this.state.name || number === this.state.number
    );

    if (validationArray.length === 0) {
      addValidatedContact(this.state);
      this.setState({ name: "", number: "" });
      this.props.onSave();
      return;
    }

    alert("This contact already exist.");
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit} className={styles.form}>
        <div className={styles.formContainer}>
          <label>
            Name
            <input
              type="text"
              name="name"
              value={this.state.name}
              pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
              title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
              required
              className={styles.input}
              onChange={this.handleInput}
            />
          </label>
          <label>
            Phone
            <input
              type="tel"
              name="number"
              value={this.state.number}
              pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
              title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
              required
              className={styles.input}
              onChange={this.handleInput}
            />
          </label>
        </div>
        <button type="submit" className={styles.button}>
          add contact
        </button>
      </form>
    );
  }
}

const mapState = (state) => ({
  contacts: selectors.getContacts(state),
});

const dispatchProps = (dispatch) => ({
  addValidatedContact: (text) => dispatch(addContact(text)),
});

export default connect(mapState, dispatchProps)(Form);
