import React from "react";
import { connect } from "react-redux";
import { filterContacts } from "../redux/phonebook-actions";
import selectors from "../redux/phonebook-selectors";
import styles from "./styles.module.css";

const Filter = ({ value, handleFilter }) => (
  <label>
    find contact
    <input
      type="text"
      className={styles.input}
      value={value}
      onChange={handleFilter}
    />
  </label>
);

const filterValue = (state) => ({
  value: selectors.getFilter(state),
});

const filterHandler = (dispatch) => ({
  handleFilter: (event) => dispatch(filterContacts(event.target.value)),
});

export default connect(filterValue, filterHandler)(Filter);
