import React, { Component } from "react";
import { createPortal } from "react-dom";
import styles from "./styles.module.css";

const modalRoot = document.querySelector("#modal-root");

class Modal extends Component {
  componentDidMount() {
    window.addEventListener("keydown", this.handleEscapeKey);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.handleEscapeKey);
  }

  handleEscapeKey = (event) => {
    event.code === "Escape" && this.props.closeModal();
  };

  handleBackdropClick = (event) => {
    event.target === event.currentTarget && this.props.closeModal();
  };

  render() {
    return createPortal(
      <div className={styles.backdrop} onClick={this.handleBackdropClick}>
        <div className={styles.modalWimdow}>{this.props.children}</div>
      </div>,
      modalRoot
    );
  }
}

export default Modal;
